import GoogleCentral from './Google.vue';
import AmazonCentral from "@/packages/Amazon";
import GithubCentral from "@/packages/Github";
const components = [
  GoogleCentral,
  AmazonCentral,
  GithubCentral
];

const install = (Vue) => {
  components.forEach(com => {
    Vue.component(com.name, com);
  });
};

export default install;
